<template>
  <div>
    <Header/>
    <mt-actionsheet
      :actions="shareActions"
      v-model="isShareActionsVisible">
    </mt-actionsheet>
    <div v-if="memoItem.ifMarkdown" class="container">
      <h3 class="title">{{memoItem.title}}</h3>
      <p class="timestamp">{{new Date(memoItem.timestamp).toLocaleTimeString()}}</p>
      <div v-html="md(memoItem.content)" class="content md"></div>
    </div>
    <div v-else class="container">
      <h3 class="title">{{memoItem.title}}</h3>
      <p class="timestamp">{{new Date(memoItem.timestamp).toLocaleTimeString()}}</p>
      <p class="content">{{memoItem.content}}</p>
    </div>
    <div class="button-group">
      <mt-button @click.native="handleModify" type="primary" plain size="large"></mt-button>
      <mt-button plain size="large" class="new-memo" @click.native="handleStar" type="default">{{ memoItem.star ? '' : '' }}</mt-button>
      <mt-button @click.native="handleShare" type="default" plain size="large"></mt-button>
      <mt-button @click.native="handleDelete" type="danger"  size="large"></mt-button>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "Vuex";
import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
import actionType from "../store/action";

import Header from "@/components/Header";

export default {
  name: "ShowMemo",
  components: {
    Header
  },
  data: function() {
    return {
        isShareActionsVisible: false
    }
  },
  computed: {
    ...mapState({
      memos: "memos"
    }),
    shareActions(){
      return [{
          name: "",
          method: () => {
            this.isShareActionsVisible = !this.isShareActionsVisible;
            let hc = require('html2canvas');
            hc(document.querySelector('div.container')).then(canvas => {
              let dataUrl = canvas.toDataURL('image/png');
              let link = document.createElement('a');
              link.href = dataUrl;
              link.download = `memo-${this.memoItem.title}-${Date.now()}`
              link.click();
            })
          }
      },{
        name: "",
        method: () => {
          document.oncopy = (e) => {
            e.clipboardData.setData('text/plain', this.memoItem.content);
            e.preventDefault();
            console.log('');
          }
          document.execCommand('copy');
        }
      }]
    },
    memoItem: function() {
      let uid = this.$route.params.id;
      return this.memos.find((item, index) => {
        return item.uid === uid;
      });
    }
  },
  beforeMount: function() {
    if (JSON.parse(window.localStorage.memos).length) {
      this.sync_memo();
    }
  },
  methods: {
    ...mapActions({
      delete_memo: actionType.DELETE_MEMO,
      star_memo: actionType.STAR_MEMO,
      sync_memo: actionType.SYNC_MEMO
    }),
    md: function(doc) {
      let MarkdownIt = require('markdown-it');
      let md = new MarkdownIt();
      let result = md.render(doc)
      return result
    },
    handleStar() {
      let uid = this.$route.params.id;
      this.star_memo(uid)
        .then(() => {
          Toast({
            message: `${this.memoItem.star ? "" : ""}`
          });
        })
        .catch(e => {
          Toast({
            message: ``
          });
          console.log(e);
        });
    },
    handleModify() {
      let uid = this.$route.params.id;
      this.$router.push({ path: `/modify/${uid}` });
    },
    handleShare(){
      this.isShareActionsVisible = !this.isShareActionsVisible;
    },
    handleDelete() {
      let uid = this.$route.params.id;
      MessageBox.confirm("", "").then(
        () => {
          console.log("");
          this.delete_memo(uid)
            .then(() => {
              Toast({
                message: ""
              });
            })
            .catch(e => {
              Toast({
                message: ""
              });
            });
          this.$router.push({ path: "/" });
        },
        () => {
          console.log("");
        }
      );
    }
  }
};
</script>

<style scoped>
.container {
  margin: 0 0.1875rem;
  margin-bottom: 0.1875rem;
}
.title {
  text-align: center;
  margin: 0.1875rem 0;
}
.timestamp {
  color: #a9a9a9;
  font-size: 13px;
  text-align: right;
  margin-right: 0.3125rem;
  margin-bottom: 0.1875rem;
}
.content {
  height: 100%;
  text-indent: 0.8125rem;
  margin-bottom: 1.25rem;
}
.button-group button {
  margin-bottom: 0.0625rem;
}
.button-group {
  text-align: center;
}
</style>



